/* eslint-disable */
import ng from 'vendor-bb-angular';

import bbStorageModuleKey, { bbStorageServiceKey } from 'lib-bb-storage-ng';
import arrangementsData from './data-bb-arrangements-http';

const arrangementsDataModuleKey = 'data-bb-arrangements-http-ng';

export const arrangementsDataKey = 'data-bb-arrangements-http-ng:arrangementsData';

export default ng
  .module(arrangementsDataModuleKey, [
    bbStorageModuleKey,
  ])
  .provider(arrangementsDataKey, [() => {
    const config = {
      baseUri: '/',
    };

    return {
      setBaseUri: (baseUri) => {
        config.baseUri = baseUri;
      },
      $get: [
        '$q',
        bbStorageServiceKey,
        '$httpParamSerializer',
        /* into */
       arrangementsData(config),
      ],
    };
  }])
  .name;



